/* You can add global styles to this file, and also import other style files */
@import "../node_modules/@angular/material/prebuilt-themes/deeppurple-amber.css";
@import "~bootstrap-icons/font/bootstrap-icons.css";
@import 'ngx-toastr/toastr';

html, body { height: 100%; }
body { 
    margin: 0;
    height: 100%;
    min-width: 300px;
    font-family: Roboto, "Helvetica Neue", sans-serif;
}

.mat-toolbar-row, .mat-toolbar-single-row {
    position: fixed;
    z-index: 999;
    background: #f5f5f517;
    border-radius: 50px;
    backdrop-filter: blur(20px);
    box-shadow: 0px 10px 20px -18px rgba(0, 0, 0, 0.25);
}
.spacer {
    flex: 1 1 auto;
}
.toolbar-icon{
    height: 35px;
    margin-right: 5px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.mat-mdc-dialog-container, .mat-mdc-dialog-container .mdc-dialog__container, .mat-mdc-dialog-container .mdc-dialog__surface {
    border-radius: 24px !important;
}

.cdk-overlay-dark-backdrop {
    backdrop-filter: blur(5px) !important;
}

.toast-container .ngx-toastr {
    padding: 15px 25px 12px 50px !important;
    border-radius: 50px;
    width: auto;
    height: 40px;
    display: flex;
    align-items: center;
}

.toast-warning {
    background-color: #f89406cc;
    backdrop-filter: blur(10px);
}

.toast-success {
    background-color: #1da475cc;
    backdrop-filter: blur(10px);
}

.tooltip-inner {
    font-size: x-small;
    border-radius: 20px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    padding-top: 6px;
    padding-right: 11px;
    padding-bottom: 6px;
    padding-left: 11px;
}

.mat-stepper-vertical, .mat-stepper-horizontal {
    display: block;
    background: none;
}

.mat-step-header .mat-step-header-ripple {
    display: none;
}

.mat-step-header:hover:not([aria-disabled]){
    background-color: rgb(0 0 0 / 0%);
    border-radius: 0px;
}

.mat-step-header .mat-step-icon-selected, .mat-step-header .mat-step-icon-state-done, .mat-step-header .mat-step-icon-state-edit {
    background-color: #4040ff;
    color: #fff;
}

.mat-stepper-horizontal-line {
    margin: 0px 10px !important;
}

.mat-step-icon {
    margin-right: 0px !important;
}

.mat-horizontal-content-container {
    padding: 0px 40px !important;
    height: 482px;
    width: 348px;
    margin-top: -30px;
}

.mat-horizontal-stepper-header {
    padding: 0 0px !important;
    width: 24px !important;
    height: 24px !important;
    cursor: pointer !important;
}

.mat-horizontal-stepper-header-container {
    width: 265px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
}

.mat-horizontal-stepper-content:not(.mat-horizontal-stepper-content-inactive) {
    visibility: inherit !important;
    margin-top: -190px;
}

.input-box{
    position: relative;
    margin-bottom: 20px;
}

.input-lbl{
    position: absolute;
    font-size: smaller;
}

.input-icon{
    position: absolute;
}

.user-input {
    background: none;
    height: 24px;
    width: 100%;
    margin-top: -22px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    border: 0;
    padding: 7px 0;
    border-bottom: 1px solid #ccc !important;
    padding-left: 25px;
    padding-right: 20px !important;
    overflow: hidden;
    text-overflow: ellipsis;
}

.user-input:focus {
    outline: none;
}

input{
    color: #435261;
}

input[type="text"] {
    box-sizing: border-box;
    letter-spacing: 1px;
}

input[type="email"] {
    box-sizing: border-box;
    letter-spacing: 1px;
}

input[type="password"] {
    box-sizing: border-box;
    letter-spacing: 1px;
}

.user-input~.focus-border {
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    /* background-color: #28b885; */
    background-image: linear-gradient(to right, #ffffff00, #29b684, 80%, #ffffff00);
    transition: 0.4s;
}

.user-input:focus~.focus-border {
    width: 100%;
    transition: 0.4s;
    left: 0;
}

.user-input:focus {
    border-bottom: 1px solid #cccccc00 !important;
    transition: 0.4s;
    left: 0;
}

input::placeholder {
    color: #9b9b9b !important;
    font-size: small;
}

.letter-plus{
    position: absolute;
    right: 56px;
    color: #435261;
}

.Index-1{
    z-index: 1 !important;
}

.loader{
    height: 20px;
    width: 20px;
}

.lbl-loading {
    font-size: small;
    color: #676767;
    margin-top: 17px;
}

.mat-mdc-menu-panel.mat-mdc-menu-panel {
    border-radius: 25px !important;
    margin-top: 12px;
}

.mat-mdc-menu-content {
    padding: 0px !important;
}

.mat-mdc-menu-item:hover:not([disabled]), 
.mat-mdc-menu-item.cdk-program-focused:not([disabled]), 
.mat-mdc-menu-item.cdk-keyboard-focused:not([disabled]), 
.mat-mdc-menu-item-highlighted:not([disabled]) {
    border-radius: 25px !important;
}